@media (max-width: 767px) {
  .wrapper {
    margin: 0 0 2.3rem 0 !important;
    width: 100%;
  }
}


.card {
  border-radius: 1rem;
  padding: 1rem;
  transition: all 0.5s cubic-bezier(.25, .8, .25, 1);
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.1);
  border: none;

  &:hover {
    box-shadow: 0 5px 10px rgba(154, 160, 185, 0.14), 0 15px 40px rgba(166, 173, 201, 0.3);
    transform: translateY(-5px);
  }
}

.wrapper {
  display: flex;
  margin: 2.3rem;
  justify-content: space-around;
  transition: all 0.5s cubic-bezier(.25, .8, .25, 1);
}

.my-card {
  width: 280px;
  height: 360px;
  border-radius: 1.5rem;
  padding: 1.5rem;
  background: white;
  position: relative;
  transition: all 0.5s cubic-bezier(.25, .8, .25, 1);
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.1);

  &-btn {
    width: 100%;

    &-item {
      margin: 1rem 0;
      width: 100%;
    }
    &-icon{
      font-size: 1.5rem;
      margin-right: .5rem;
    }
  }
}

.my-card:hover:before {
  opacity: 1;
}

.my-card:hover .info {
  opacity: 1;
  transform: translateY(0px);

}

.my-card:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2;
  transition: 0.5s;
  opacity: 0;
}

.my-card img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 1rem;
}
.img{
  border-radius: .9rem;
}
.my-card .info {
  position: relative;
  z-index: 3;
  color: white;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.5s;
}

.my-card .info h1 {
  margin: 0;
}

.my-card .info p {
  letter-spacing: 1px;
  font-size: 15px;
  margin-top: 8px;
}

.my-card .info button {
  padding: 0.6rem;
  outline: none;
  border: none;
  border-radius: 1rem;
  background: white;
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: 0.4s ease;
}

.my-card .info button:hover {
  background: dodgerblue;
  color: white;
}
